import React from 'react';
import { Form, Input, Button, Checkbox } from 'antd';
import {Link} from "react-router-dom";

import { UserOutlined, LockOutlined } from '@ant-design/icons';


import "./Login.css";
import LoginHeader from '../LoginHeader/LoginHeader';
import LoginFooter from '../LoginFooter/LoginFooter';












const Login = () => {

  const onFinish = values => {
    console.log('Received values of form: ', values);
  };

    return (
      <>
             <LoginHeader />
             <div className="mainloginscreen">  
                  <div className="mainsubloginscreen">      
                    <div className="login_formfield">
                    <Form
                        name="normal_login"
                        className="login-form"
                        initialValues={{ remember: true }}
                        onFinish={onFinish}
                      >
                        <h4>Sign in to your account</h4>
                        <Form.Item
                          name="username"
                          rules={[{ required: true, message: 'Please input your email!' }]}
                        >
                          <Input placeholder="Enter email" />
                        </Form.Item>
                        <Form.Item
                          name="password"
                          rules={[{ required: true, message: 'Please input your Password!' }]}
                        >
                          <Input                      
                            type="password"
                            placeholder="Enter password"
                          />
                        </Form.Item>
                        <Form.Item className="rememberfield">
                          <Form.Item name="remember" noStyle>
                            <Checkbox>Remember me</Checkbox>
                          </Form.Item>

                          <Link className="login-form-forgot" to="/resetpassword">
                            Forgot password
                          </Link>
                        </Form.Item>

                        <Form.Item>
                          <Link type="primary" htmlType="submit" className="login-form-button">
                              SIGN IN
                          </Link>
                          <div className="signupfield">
                              Don't have an account? <Link to="">Sign up</Link>
                          </div>                    
                        </Form.Item>
                      </Form>

                      

              </div>
                    <LoginFooter/>
                  </div>      
             </div>
             
             
      </>
    )
}
export default Login;